import uniListItem from '@dcloudio/uni-ui/lib/uni-list-item/uni-list-item.vue';
export default {
	components: { uniList, uniListItem },
	data() {
		return {
			login:uni.getStorageSync('login_key').login
		};
	},
	onLoad() {
	},
	methods: {
		goBack:function(){
			uni.navigateBack({
				
			})
		},
		infoin:function() {
			uni.navigateTo({
				url:"../info/info"
			})
		},
		logout: function() {
			console.log('log out');
			uni.removeStorageSync('login_key');
			uni.showToast({
				title: '已经退出当前账号'
			});
			uni.navigateBack();
		}
	}
};
